import { __ } from '@wordpress/i18n';
import {
	useBlockProps,
	InspectorControls,
	MediaUpload,
	MediaUploadCheck,
	BlockControls,
	RichText,
} from '@wordpress/block-editor';

import {
	PanelBody,
	Panel,
	PanelRow,
	ToolbarGroup,
	ToolbarButton,
	Button,
	TabPanel,
	Card,
	CardBody,
	CardDivider,
	ToggleControl,
	RangeControl,
	__experimentalBorderControl as BorderControl,
} from '@wordpress/components';

const { Fragment } = wp.element;

// editor style
import './editor.scss';

// colors
import ColorControl from '../../utilities/components/colorcontrol/colorcontrol';
import Colors from '../../utilities/colors-palette';
import { PinItStyles } from '../styled-elements/styled-blocks';
import SingleInput from '../../utilities/components/singleinput/singleinput';
import FlexGutenMarginPadding from '../../utilities/components/margincontrol/margincontrol';

export default function Edit({ attributes, setAttributes, clientId }) {
	const {
		id,
		backgroundColor,
		titleColor,
		subTitleColor,
		photo,
		cardTitle,
		cardSubTitle,
		cardTitleFontSize,
		cardTitleMargin,
		cardSubTitleFontSize,
		cardSubTitleMargin,
		buttonMargin,
		buttonLabel,
		enableContainerBorder,
		containerBorder,
		containerBorderRadius,
		imageHeight,
		imageWidth,
		buttonTextColor,
		buttonBackgroundColor,
	} = attributes;
	setAttributes({
		id: 'flexguten-' + clientId.slice(0, 8),
	});
	return (
		<Fragment>
			{photo && (
				<BlockControls>
					<ToolbarGroup>
						<MediaUpload
							onSelect={(media) =>
								setAttributes({
									photo: media,
								})
							}
							allowedTypes={['image']}
							value={photo && photo.id}
							render={({ open }) => (
								<ToolbarButton
									onClick={open}
									label="Edit"
									icon="edit"
								/>
							)}
						/>
					</ToolbarGroup>
				</BlockControls>
			)}
			<InspectorControls>
				<TabPanel
					className="flexguten-tab-panel"
					activeClass="flexguten-active"
					tabs={[
						{
							name: 'flexguten_settings_tab',
							title: 'Settings',
							className:
								'flexguten-settings-tab flexguten-general',
						},
						{
							name: 'flexguten_styles_tab',
							title: 'Styles',
							className: 'flexguten-style-tab flexguten-general',
						},
					]}
				>
					{(tab) => {
						if (tab.name === 'flexguten_settings_tab') {
							return (
								<Panel>
									<PanelBody
										title={__(
											'Container Settings',
											'flexguten'
										)}
										initialOpen={true}
									>
										<ToggleControl
											label={__(
												'Container Border',
												'flexguten'
											)}
											checked={enableContainerBorder}
											onChange={() =>
												setAttributes({
													enableContainerBorder:
														!enableContainerBorder,
												})
											}
											help={
												enableContainerBorder
													? __(
															'Container Border Enabled',
															'flexguten'
													  )
													: __(
															'Container Border Disabled',
															'flexguten'
													  )
											}
										/>

										{enableContainerBorder && (
											<div>
												<BorderControl
													colors={Colors}
													label={__(
														'Border Settings',
														'flexguten'
													)}
													onChange={(value) =>
														setAttributes({
															containerBorder:
																value,
														})
													}
													value={containerBorder}
													withSlider={true}
													shouldSanitizeBorder={true}
												/>
												<br />
												<RangeControl
													label={__(
														'Border Radius',
														'flexguten'
													)}
													value={
														containerBorderRadius
													}
													onChange={(value) =>
														setAttributes({
															containerBorderRadius:
																value,
														})
													}
													min={0}
													max={100}
													help={
														__(
															'border radius: ',
															'flexguten'
														) +
														containerBorderRadius +
														'px'
													}
													allowReset={true}
													resetFallbackValue={15}
												/>
											</div>
										)}
									</PanelBody>
									<PanelBody
										title={__(
											'Content Settings',
											'flexguten'
										)}
										initialOpen={false}
									>
										<Card>
											<CardBody className="test-class">
												<FlexGutenMarginPadding
													label={__(
														'Title Margin',
														'flexguten'
													)}
													attribute={cardTitleMargin}
													attributeName="cardTitleMargin"
													setAttributes={
														setAttributes
													}
												/>
												<CardDivider />

												<FlexGutenMarginPadding
													label={__(
														'Description Margin',
														'flexguten'
													)}
													attribute={
														cardSubTitleMargin
													}
													attributeName="cardSubTitleMargin"
													setAttributes={
														setAttributes
													}
												/>
												<CardDivider />
												<FlexGutenMarginPadding
													label={__(
														'Button Margin',
														'flexguten'
													)}
													attribute={buttonMargin}
													attributeName="buttonMargin"
													setAttributes={
														setAttributes
													}
												/>
											</CardBody>
										</Card>
										<br />
									</PanelBody>
									<PanelBody
										title={__(
											'Image Settings',
											'flexguten'
										)}
										initialOpen={false}
									>
										<RangeControl
											label={__(
												'Image Height',
												'flexguten'
											)}
											value={imageHeight}
											onChange={(value) =>
												setAttributes({
													imageHeight: value,
												})
											}
											min={1}
											max={500}
											allowReset={true}
											resetFallbackValue="250"
										/>
										<RangeControl
											label={__(
												'Image Width',
												'flexguten'
											)}
											value={imageWidth}
											onChange={(value) =>
												setAttributes({
													imageWidth: value,
												})
											}
											min={1}
											max={100}
											allowReset={true}
											resetFallbackValue="100"
										/>
									</PanelBody>
								</Panel>
							);
						} else if (tab.name === 'flexguten_styles_tab') {
							return (
								<Panel>
									<PanelBody
										title={__('Colors', 'flexguten')}
										initialOpen={true}
									>
										<PanelRow>
											<p className="flexguten-label">
												{__('Container', 'flexguten')}
											</p>
										</PanelRow>
										<ColorControl
											label={__(
												'Background Color',
												'flexguten'
											)}
											colorValue={backgroundColor}
											colorName="backgroundColor"
											setAttributes={setAttributes}
										/>
										<PanelRow>
											<p className="flexguten-label">
												{__('Heading', 'flexguten')}
											</p>
										</PanelRow>
										<ColorControl
											label={__(
												'Heading Color',
												'flexguten'
											)}
											colorValue={titleColor}
											colorName="titleColor"
											setAttributes={setAttributes}
										/>
										<PanelRow>
											<p className="flexguten-label">
												{__('Description', 'flexguten')}
											</p>
										</PanelRow>
										<ColorControl
											label={__(
												'Description Color',
												'flexguten'
											)}
											colorValue={subTitleColor}
											colorName="subTitleColor"
											setAttributes={setAttributes}
										/>
										<PanelRow>
											<p className="flexguten-label">
												{__('Button', 'flexguten')}
											</p>
										</PanelRow>
										<ColorControl
											label={__(
												'Background Color',
												'flexguten'
											)}
											colorValue={buttonBackgroundColor}
											colorName="buttonBackgroundColor"
											setAttributes={setAttributes}
										/>
										<ColorControl
											label={__(
												'Text Color',
												'flexguten'
											)}
											colorValue={buttonTextColor}
											colorName="buttonTextColor"
											setAttributes={setAttributes}
										/>
									</PanelBody>
									<PanelBody
										title={__('Font Sizes', 'flexguten')}
										initialOpen={false}
									>
										<SingleInput
											label={__(
												'Heading Font Size',
												'flexguten'
											)}
											attribute={cardTitleFontSize}
											attributeName="cardTitleFontSize"
											setAttributes={setAttributes}
											min={0}
											max={200}
											deskResetValue={20}
											tabResetValue={20}
											mobResetValue={15}
											unit="px"
										/>
										<SingleInput
											label={__(
												'Description Font Size',
												'flexguten'
											)}
											attribute={cardSubTitleFontSize}
											attributeName="cardSubTitleFontSize"
											setAttributes={setAttributes}
											min={0}
											max={200}
											deskResetValue={20}
											tabResetValue={20}
											mobResetValue={15}
											unit="px"
										/>
									</PanelBody>
								</Panel>
							);
						}
					}}
				</TabPanel>
			</InspectorControls>

			<PinItStyles
				backgroundColor={backgroundColor}
				titleColor={titleColor}
				subTitleColor={subTitleColor}
				containerBorder={containerBorder}
				containerBorderRadius={containerBorderRadius}
				cardTitleMargin={cardTitleMargin}
				cardTitleFontSize={cardTitleFontSize}
				cardSubTitleMargin={cardSubTitleMargin}
				cardSubTitleFontSize={cardSubTitleFontSize}
				buttonMargin={buttonMargin}
				imageHeight={imageHeight}
				imageWidth={imageWidth}
				buttonBackgroundColor={buttonBackgroundColor}
				buttonTextColor={buttonTextColor}
				{...useBlockProps()}
			>
				<div className="pinit-container">
					<div className="pinit-left">
						<div className="vertical">
							<RichText
								tagName="div"
								className="flexguten-pinit-card-title"
								value={cardTitle}
								onChange={(value) =>
									setAttributes({
										cardTitle: value,
									})
								}
								placeholder={__(
									'No time to read now?',
									'flexguten'
								)}
							/>
							<RichText
								tagName="div"
								className="flexguten-pinit-card-subtitle"
								value={cardSubTitle}
								onChange={(value) =>
									setAttributes({
										cardSubTitle: value,
									})
								}
								placeholder={__(
									'No worries! Save on Pinterest to read it later.',
									'flexguten'
								)}
							/>
							<div className="pinterest-button">
								<RichText
									className="flexguten-pinit-card-buttontext"
									value={buttonLabel}
									onChange={(value) =>
										setAttributes({
											buttonLabel: value,
										})
									}
								/>
								<svg
									clipRule="evenodd"
									fillRule="evenodd"
									strokeLinejoin="round"
									strokeMiterlimit="2"
									viewBox="0 0 24 24"
								>
									<path
										d="m14.523 18.787s4.501-4.505 6.255-6.26c.146-.146.219-.338.219-.53s-.073-.383-.219-.53c-1.753-1.754-6.255-6.258-6.255-6.258-.144-.145-.334-.217-.524-.217-.193 0-.385.074-.532.221-.293.292-.295.766-.004 1.056l4.978 4.978h-14.692c-.414 0-.75.336-.75.75s.336.75.75.75h14.692l-4.979 4.979c-.289.289-.286.762.006 1.054.148.148.341.222.533.222.19 0 .378-.072.522-.215z"
										fillRule="nonzero"
									/>
								</svg>
							</div>
						</div>
					</div>
					<div className="pinit-right">
						{photo ? (
							<div className="review-image">
								<img
									src={photo.url}
									alt={photo.alt ? photo.alt : 'photo title'}
								/>
							</div>
						) : (
							<div className="review-image-button">
								<MediaUploadCheck>
									<MediaUpload
										onSelect={(media) =>
											setAttributes({
												photo: media,
											})
										}
										allowedTypes={['image']}
										value={photo && photo.id}
										render={({ open }) => (
											<Button
												onClick={open}
												variant="secondary"
												icon="upload"
												className="scb-image-upload-btn"
											>
												{__(
													'Add Image',
													'simple-card-block'
												)}
											</Button>
										)}
									/>
								</MediaUploadCheck>
							</div>
						)}
					</div>
				</div>
			</PinItStyles>
		</Fragment>
	);
}
