import styled from 'styled-components';

export const AmzStyles = styled.div`
	// Container Styles

	background: ${(props) => props.containerBg};
	${(props) =>
		`border: ${props.containerBorder.width} ${props.containerBorder.style} ${props.containerBorder.color};`}
	border-radius:${(props) => props.containerBorderRadius}px;

	.review-image img {
		height: ${(props) => props.imageHeightWidth.height}%;
		width: ${(props) => props.imageHeightWidth.width}%;
	}

	.star-rating .react-rater-star.is-active {
		color: ${(props) => props.starRatingColor};
	}

	.react-rater-star.is-active-half:before {
		color: ${(props) => props.starRatingColor};
	}

	.rating-count p {
		color: ${(props) => props.reviewTextColor};
		font-size: ${(props) => props.ReviewTextFontSize}px;
	}

	.review-text h3 {
		color: ${(props) => props.headingFontColor};
		font-size: ${(props) => props.headingFontSize}px;
	}

	.product-features li {
		color: ${(props) => props.featuresColor};
		font-size: ${(props) => props.featuresFontSize}px;
	}

	.flexguten-product-price {
		color: ${(props) => props.pricingColor};
		font-size: ${(props) => props.pricingFontSize}px;
	}

	.amazon-button {
		background: ${(props) => props.buttonBackground}!important;
		color: ${(props) => props.buttonFontColor};
		font-size: ${(props) => props.buttonFontSize}px;
		${(props) =>
			`border: ${props.buttonBorder.width} ${props.buttonBorder.style} ${props.buttonBorder.color};`}
	}

	.amazon-button:hover {
		background: ${(props) => props.buttonHoverBackground}!important;
		color: ${(props) => props.buttonHoverFontColor};
	}
`;

export const PinItStyles = styled.div`
	.pinit-container {
		background-color: ${(props) => props.backgroundColor};

		${(props) =>
			props.containerBorder.width !== '0px' &&
			`border: ${props.containerBorder.width} ${
				props.containerBorder.style
					? props.containerBorder.style
					: 'solid'
			} ${
				props.containerBorder.color
					? props.containerBorder.color
					: '#eee'
			};`}

		${(props) =>
			props.containerBorderRadius &&
			`border-radius: ${props.containerBorderRadius}px;`}
		
		.pinit-left {
			.flexguten-pinit-card-title {
				${(props) =>
					`margin: ${
						props.cardTitleMargin.top
							? props.cardTitleMargin.top
							: 0
					}px  ${
						props.cardTitleMargin.right
							? props.cardTitleMargin.right
							: 0
					}px  ${
						props.cardTitleMargin.bottom
							? props.cardTitleMargin.bottom
							: 0
					}px  ${
						props.cardTitleMargin.left
							? props.cardTitleMargin.left
							: 0
					}px; 
					`}
					color: ${(props) => props.titleColor};
					
			}
			.flexguten-pinit-card-subtitle {
				${(props) =>
					`margin: ${
						props.cardSubTitleMargin.top
							? props.cardSubTitleMargin.top
							: 0
					}px  ${
						props.cardSubTitleMargin.right
							? props.cardSubTitleMargin.right
							: 0
					}px  ${
						props.cardSubTitleMargin.bottom
							? props.cardSubTitleMargin.bottom
							: 0
					}px  ${
						props.cardSubTitleMargin.left
							? props.cardSubTitleMargin.left
							: 0
					}px; 
					`}
					color: ${(props) => props.subTitleColor};
			}
			.pinterest-button {
				${(props) =>
					`margin: ${
						props.buttonMargin.top ? props.buttonMargin.top : 0
					}px  ${
						props.buttonMargin.right ? props.buttonMargin.right : 0
					}px  ${
						props.buttonMargin.bottom
							? props.buttonMargin.bottom
							: 0
					}px  ${
						props.buttonMargin.left ? props.buttonMargin.left : 0
					}px;`}
				background-color: ${(props) => props.buttonBackgroundColor};
				color: ${(props) => props.buttonTextColor};
				svg{
					fill: ${(props) => props.buttonTextColor};
				}
			}
	}

	@media (min-width: 1025px) {
		
		.flexguten-pinit-card-title {
			font-size: ${(props) => props.cardTitleFontSize.desktop}px;
		}
		.flexguten-pinit-card-subtitle {
			font-size: ${(props) => props.cardSubTitleFontSize.desktop}px;
		}
		.review-image img {
			height: ${(props) => props.imageHeight}px;
			width: ${(props) => props.imageWidth}%;
		}
		
	}
	@media (min-width: 768px) and (max-width: 1024px) {

		.flexguten-pinit-card-title {
			font-size: ${(props) => props.cardTitleFontSize.tablet}px;
		}
		.flexguten-pinit-card-subtitle {
			font-size: ${(props) => props.cardSubTitleFontSize.tablet}px;
		}		
		.review-image img {
			height: ${(props) => props.imageHeight}px;
			width: ${(props) => props.imageWidth}%;
		}

	}
	@media (max-width: 767px) {
		
		.flexguten-pinit-card-title {
			font-size: ${(props) => props.cardTitleFontSize.mobile}px;
		}
		.flexguten-pinit-card-subtitle {
			font-size: ${(props) => props.cardSubTitleFontSize.mobile}px;
		}	
		.review-image img {
			height: ${(props) => props.imageHeight}px;
			width: ${(props) => props.imageWidth}%;
		}

	}
`;

export const PgbStyle = styled.div`
	// Container Styles
	.post-grid-main {
		background-color: ${(props) => props.containerBg};
		padding: ${(props) => props.conatainerPadding}px;
		grid-template-columns: ${(props) =>
			`repeat(${props.numberofRows}, 1fr)`};

		& .content-section {
			background-color: ${(props) => props.contentBg};
			.post-title h4 a {
				color: ${(props) => props.headingColor};
			}
			.post-excerpt {
				color: ${(props) => props.excerptColor};
			}
			.content-hyperlink a {
				color: ${(props) => props.readMoreColor};
			}
		}
	}

	@media (min-width: 1025px) {
		.post-title h4 a {
			font-size: ${(props) => props.headingFontSizes.desktop}px;
		}
		.post-excerpt {
			font-size: ${(props) => props.excerptFontSizes.desktop}px;
		}
		.content-hyperlink a {
			font-size: ${(props) => props.readMoreFontSizes.desktop}px;
		}
	}
	@media (min-width: 768px) and (max-width: 1024px) {
		.post-title h4 a {
			font-size: ${(props) => props.headingFontSizes.tablet}px;
		}
		.post-excerpt {
			font-size: ${(props) => props.excerptFontSizes.tablet}px;
		}
		.content-hyperlink a {
			font-size: ${(props) => props.readMoreFontSizes.tablet}px;
		}
	}
	@media (max-width: 767px) {
		.post-title h4 a {
			font-size: ${(props) => props.headingFontSizes.mobile}px;
		}
		.post-excerpt {
			font-size: ${(props) => props.excerptFontSizes.mobile}px;
		}
		.content-hyperlink a {
			font-size: ${(props) => props.readMoreFontSizes.mobile}px;
		}
	}
`;
