/**
 * Import Essential Files
 */
import { __ } from '@wordpress/i18n';
import './margincontrol.scss';
import NumericInput from 'react-numeric-input';

const FlexGutenMarginPadding = ({
	label,
	MCTop,
	MCRight,
	MCBottom,
	MCLeft,
	attribute,
	attributeName,
	setAttributes,
}) => {
	return (
		<div className="flexguten-margin-padding-section">
			<div className="flexguten-section-label">{label}</div>
			<div className="flexguten-border-main">
				<div className="border-width">
					<p className="flexguten-label">{__('Top', 'flexguten')}</p>
					<NumericInput
						className="number-input-box"
						value={attribute.top ? attribute.top : 0}
						onChange={(value) =>
							setAttributes({
								[attributeName]: {
									...attribute,
									top: value,
								},
							})
						}
						min={0}
						max={100}
						size={1}
					/>
				</div>

				<div className="border-width">
					<p className="flexguten-label">
						{__('Right', 'flexguten')}
					</p>
					<NumericInput
						className="number-input-box"
						value={attribute.right ? attribute.right : 0}
						onChange={(value) =>
							setAttributes({
								[attributeName]: {
									...attribute,
									right: value,
								},
							})
						}
						min={0}
						max={100}
						size={1}
					/>
				</div>

				<div className="border-width">
					<p className="flexguten-label">
						{__('Bottom', 'flexguten')}
					</p>
					<NumericInput
						className="number-input-box"
						value={attribute.bottom ? attribute.bottom : 0}
						onChange={(value) =>
							setAttributes({
								[attributeName]: {
									...attribute,
									bottom: value,
								},
							})
						}
						min={0}
						max={100}
						size={1}
					/>
				</div>

				<div className="border-width">
					<p className="flexguten-label">{__('Left', 'flexguten')}</p>
					<NumericInput
						className="number-input-box"
						value={attribute.left ? attribute.left : 0}
						onChange={(value) =>
							setAttributes({
								[attributeName]: {
									...attribute,
									left: value,
								},
							})
						}
						min={0}
						max={100}
						size={1}
					/>
				</div>
			</div>
		</div>
	);
};

export default FlexGutenMarginPadding;
